import React, {useState, useEffect} from 'react';
import { Card, Button } from 'antd';
import axios from 'axios';

import './App.css';
import { Meta } from 'antd/lib/list/Item';

const {meta} = Card;


function App() {
  const [news, setNews] = useState([]);

  useEffect(() => {
    const loadNews = async () => {
      const response = await axios.get(
        "https://newsapi.org/v2/everything?q=Apple&from=2022-03-17&sortBy=popularity&apiKey=e21cc67d1c594cffa9cc081890190254");
      setNews(response.data.articles);

    };
    loadNews();
  }, []);

  console.log("news", news);


  return (
    <div className='App'>
      
          {news && news.map((item, index) => {
            return (
              <Card hoverable style={{width: "70%"}}
              cover={<img src={item.urlToImage} alt='image'/>}>
              
                <Meta title={item.title} description={item.content}/>
                <a href={item.url} target='_blank' rel='noopener norefferer'>
                <Button type='btn btn-primary' style={{margin: '10px', padding: '2px'}}>Read More</Button>
                </a>

              </Card>
            );
          })};


      
    </div>
  );
}

export default App;
